////
/// Global Header
/// @group patterns
////
.global-header {
    &:not(.global-header--withpromo) {
        @include box-shadow();
    }

    background-color: $color-purewhite;
    position: relative;
    z-index: z-index('unveiled');

    @include media('print') {
        display: none;
    }

    .global-header__content {
        position: relative;

        @include media('<small') {
            align-items: flex-start;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
        }

        @include media('>=small') {
            @include standard-box();
            @include standard-grid();

            grid-template-rows: repeat(3, auto);
        }

        @include media('>=desktop') {
            grid-row-gap: $measure--basic;
            grid-template-rows: repeat(2, auto);
        }
    }
}

.header-logo {
    @include media('<small') {
        align-self: center;
        justify-content: flex-start;
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--half;
        padding-top: $measure--half;
        width: $measure--huge5;
    }

    @include media('>=small') {
        grid-column-end: span 3;
        grid-column-start: 1;
        grid-row-end: span 2;
        grid-row-start: 1;
    }

    a {
        @include anchor-img();

        display: inline-block;
        width: 100%;

        @include media('<small') {
            height: $measure--medium7;
        }

        @include media('>=small') {
            margin-bottom: $measure--small2;
            margin-top: $measure--small2;
        }

        .logo {
            width: 100%;

            @include media('<small') {
                height: 100%;
            }
        }
    }
}

.global-header__utility {
    @include media('>=small') {
        display: flex;
        grid-column-end: 24;
        grid-column-start: 16;
        justify-content: flex-end;
        align-items: center;
        grid-row-end: span 2;
        grid-row-start: 1;

        .global-header__utility__e {
            padding: $measure--half;
            //display: flex;

            min-height: 3rem;
            width: 100%;
            // a {
            //     display: block;
            // }
        }
    }
}

.promo-strap {
    background-color: $color-pink90;
    background: linear-gradient(
        90deg,
        $color-red60 0%,
        $color-pink80 70%,
        $color-pink90 100%
    );
    color: $color-purewhite;
    padding-bottom: $measure--basic;
    padding-top: $measure--basic;

    .promo-strap__content {
        @include h6style();
        @include standard-box();

        font-weight: $font-weight-normal;
        margin-top: 0;
        text-align: center;

        sup,
        sub {
            font-size: $font-size--super;
            line-height: $font-size--super;
        }

        a {
            @include underline-basic(
                $inactive-color: $color-purewhite,
                $hover-color: $color-purewhite,
                $underline-width: $border-width-default,
                $outline: $color-purewhite
            );
        }
    }
}
